@import "../../styles/setup.scss";

.page-login {
  background-color: black;
  width: 100%;

  @media only screen and (max-width: $mobile-screen-width) {
    padding: 0;
  }

  .block-container {
    height: 80vh;

    @media only screen and (max-width: $mobile-screen-width) {
      height: 90vh;
    }

    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    color: white;
  }

  .login-logo-container {
    height: 30%;
    // background-color: $stratos-green-ligthest;

    @media only screen and (max-width: $mobile-screen-width) {
      height: 25%;
    }

    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    width: 80%;
    max-width: 400px;
    padding-bottom: 50px;

    @media only screen and (max-width: $mobile-screen-width) {
      padding-bottom: 0px;
      width: 85%;
      max-width: 310px;
    }

    .image {
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;

      // background-color: aqua;
      height: 100%;
      width: 240px;
      @media only screen and (max-width: $mobile-screen-width) {
        width: 180px;
      }

      svg {
        height: 100px;
        width: 100%;
        // background-color: brown;
      }
    }

    .logo-title {
      // margin-left: 10px;
      // background-color: $stratos-outbound-trafic-color;
      margin-bottom: 25px;

      @media only screen and (max-width: $mobile-screen-width) {
        margin-bottom: 30px;
      }
    }
  }

  .form-container {
    height: 70%;
    width: 60%;
    min-width: 800px;

    @media only screen and (max-width: $mobile-screen-width) {
      width: 90%;
      min-width: 120px;
    }

    display: flex;
    flex-direction: column;

    .form-title {
      @media only screen and (max-width: $mobile-screen-width) {
        flex-direction: column;
      }

      display: flex;
      justify-content: center;
      align-items: center;

      margin-bottom: 5px;
      height: 50px;

      @media only screen and (max-width: $mobile-screen-width) {
        height: 90px;
      }

      display: flex;
      justify-content: space-between;

      .form-logo-title-container {
        display: flex;
        align-items: center;

        // background-color: brown;

        .form-logo-title {
          margin-right: 10px;
        }
      }
      .paste-button-container {
        height: 50px;

        .button-submit {
          border-width: 1px;
          transition: all 0.3s;

          height: 40px;

          background: black;
          border: 2px solid $stratos-green-ligth;

          color: $stratos-green-ligth;

          &:hover {
            color: $stratos-green-ligther !important;
            border: 2px solid $stratos-green-ligther;
          }
          &.inactive {
            // color: #404040;
            cursor: not-allowed;
            background-color: black;
            border: 1px solid #404040 !important;

            // &:hover {
            //   color: $stratos-border-color !important;
            //   border: 1px solid $stratos-border-color !important;
            // }
          }
          // &:hover {
          //   color: $stratos-green-ligther !important;
          //   border-color: $stratos-green-ligther;
          //   background-color: black;
          // }

          &.button:focus {
            border-color: white;
          }
        }
      }
    }

    .form-content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

      .link-container {
        width: 100%;

        display: flex;
        overflow: hidden;

        min-height: 200px;
        max-height: 220px;

        &.input-container {
          min-height: 50px;
        }

        @media only screen and (max-width: $mobile-screen-width) {
          min-height: 190px;
          max-height: 250px;
        }

        justify-content: center;

        .link-input-container {
          width: 100%;

          textarea,
          input {
            border-radius: 12px;
            border: none;
            padding: 8px 15px;
            width: 100%;
            color: $stratos-green-ligther;
            &:focus {
              outline: none;
            }
          }

          .error-message {
            margin-top: 5px;
          }
        }
      }

      .go-button-container {
        width: 40%;
        &.double-button {
          width: 99%;
        }

        @media only screen and (max-width: $mobile-screen-width) {
          width: 100%;
        }

        padding-top: 5px;
        padding-bottom: 5px;

        .button-submit {
          height: 40px;

          @media only screen and (max-width: $mobile-screen-width) {
            height: 40px;
          }

          background: black;
          border: 2px solid $stratos-green-ligth;

          color: $stratos-green-ligth;

          &:hover {
            color: $stratos-green-ligther !important;
            border: 2px solid $stratos-green-ligther;
          }
          &.inactive {
            color: #404040;
            cursor: not-allowed;
            background-color: black;
            border: 1px solid #404040 !important;

            &:hover {
              color: $stratos-border-color !important;
              border: 1px solid $stratos-border-color !important;
            }
          }

          transition: all 0.5s;
        }
      }
    }
  }
}
